body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: Roboto, "Century Gothic", Arial;
  margin: 0;
  padding: 0;
}

html,
body,
#root {
  height: 100%;
}

#container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightgreen;
  height: 100%;
}

#drum-machine {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 10px;
  padding: 10px;
}

#pad {
  width: 300px;
  height: 300px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 5px;
}

#pad .drum-pad {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid lightgrey;
  font-size: 16px;
  color: grey;
  font-weight: 400;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 20px;
  width: 100%;
  height: 100%;
  cursor: pointer;
  border-radius: 10px;
}

#display {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 300px;
  height: 300px;
}

.display-block {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid lightgrey;
  font-size: 16px;
  color: grey;
  font-weight: 400;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 60%;
  height: 50px;
  border-radius: 10px;
}

#slider {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: lightgrey;
  outline: none;
  width: 80%;
  height: 6px;
  border-radius: 3px;
}

#slider::-moz-range-thumb {
  -webkit-appearance: none;
  border: 2px solid #b4b4b4;
  background: white;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  box-sizing: border-box;
}

#side-text {
  margin-left: 40px;
  font-size: 100px;
  font-weight: 100;
  color: white;
  position: absolute;
  left: 0;
  bottom: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@media screen and (max-width: 1000px) {
  #container,
  #drum-machine {
    flex-direction: column;
  }
  #side-text {
    margin-left: 0;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
  }
}
